import {
  Avatar,
  Badge,
  TableBody,
  TableCell,
  TableRow,
} from "@windmill/react-ui";
import MainDrawer from "components/Properties/EditPropertiesModal";
import ProductDrawer from "components/food/EditFood";
import DeleteModal from "components/food/DeleteFood";
import EditDeleteButton from "components/table/EditDeleteButton";
import ShowHideButton from "components/table/ShowHideButton";
import defalutimage from "../../assets/img/forgot-password-office-dark.jpeg";
import Tooltip from "components/tooltip/Tooltip";
import useToggleDrawer from "hooks/useToggleDrawer";
import { t } from "i18next";
import { useState, useContext } from "react";
import { FiEdit, FiTrash2, FiZoomIn } from "react-icons/fi";
import { Link } from "react-router-dom";
import { showingTranslateValue } from "utils/translate";
import { useHistory, useLocation } from "react-router-dom";
import moment from "moment";
import { SidebarContext } from "context/SidebarContext";
import { getalcareteById } from "services/Api";

//internal import

const ProductTable = ({
  products,
  isCheck,
  getAllFoodList,
  singleFood,
  setsingleFood,
  getAllCoupansList,
  pagenumber,
  wholeCount,
}) => {
  const { toggleModal, toggleDrawer1, toggleDrawer,setIsDrawerOpen1 } =
    useContext(SidebarContext);
  // const { title } = useToggleDrawer();
  const [serviceId, setServiceId] = useState("");
  const [title, setTitle] = useState("");
  const history = useHistory();
  const format = "MM/DD/YYYY";
  const handleUpdate = async (e) => {
    console.log(pagenumber,"pagenumber")
    const FoodReview = await getalcareteById(e.id);
    if (FoodReview && FoodReview.status === 200) {
      console.log(FoodReview?.data?.data?.Ala_carte,"singleview")
      setsingleFood(FoodReview?.data?.data?.Ala_carte)
    }
    toggleDrawer1();
  };
  const handleModalOpen = (data) => {
    console.log(data, "DELETEsww", "ww");
    setServiceId(data.id);
    setsingleFood(data);
    toggleModal();
    setTitle(title);
  };

  return (
    <>
      <DeleteModal
        getAllFoodList={getAllFoodList}
        id={serviceId}
        title={title}
        singleFood={singleFood}
      />
      <MainDrawer>
        <ProductDrawer
          isOpen={toggleDrawer1}
          pg={pagenumber}
          getAllFoodList={getAllFoodList}
          onClose={toggleDrawer1}
          singleFood={singleFood}
          setsingleFood={setsingleFood}
        />
      </MainDrawer>
      <TableBody>
        {products?.map((properties, i) => (
          <TableRow key={i + 1}>
            <TableCell>
            <span className="text-sm">{(wholeCount?.currentPage-1)*wholeCount?.pageSize+i+1}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm ">{properties?.Property?.name}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm ">{properties?.name}</span>
            </TableCell>
            <TableCell className="textcorrect">
              <span className="text-sm ">
                {properties?.description?.length > 150
                  ? (properties?.description).substring(0, 150 - 3) + "..."
                  : properties?.description}
              </span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm ">{properties?.type}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{properties?.category}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{properties?.no_of_skewers}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{"AMOUNT : " + properties?.price}</span>
              , <br />
              <span className="text-sm">{"CGST : " + properties?.cgst}</span>,
              <br />{" "}
              <span className="text-sm">{"SGST : " + properties?.sgst}</span>
            </TableCell>
            <TableCell>
              {properties?.Ala_carte_images?.map((img) => (
                <img
                  className="object-cover imgCustom"
                  src={img.image}
                  alt="Office"
                />
              ))}
            </TableCell>
            {/* <TableCell className="text-center">
              <ShowHideButton id={properties._id} status={properties.status} />
            </TableCell> */}
            <TableCell>
              <button
                onClick={() => {
                  handleUpdate(properties);
                }}
                className="p-2 cursor-pointer text-gray-400 hover:text-green-600 focus:outline-none"
              >
                <Tooltip
                  id="edit"
                  Icon={FiEdit}
                  title={t("Edit")}
                  bgColor="#10B981"
                />
              </button>
              <button
                onClick={() => handleModalOpen(properties)}
                className="p-2 cursor-pointer text-gray-400 hover:text-red-600 focus:outline-none"
              >
                <Tooltip
                  id="delete"
                  Icon={FiTrash2}
                  title={t("Delete")}
                  bgColor="#EF4444"
                />
              </button>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default ProductTable;
