import * as dayjs from "dayjs";
import { useContext, useState } from "react";
import { useForm } from "react-hook-form";
import { useSelector } from "react-redux";
import { SidebarContext } from "context/SidebarContext";
import { notifyError, notifySuccess } from "utils/toast";
import { addAllCarte } from "services/Api";

const useCouponSubmit = (id, getAllFoodList, pagenumber,pg) => {
  const { isDrawerOpen, closeDrawer,closeDrawer1,toggleDrawer, setIsUpdate, lang } =
    useContext(SidebarContext);
  const [imageUrl, setImageUrl] = useState([]);
  const [language, setLanguage] = useState(lang);
  const [published, setPublished] = useState(false);
  const [discountType, setDiscountType] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const settings = useSelector((state) => state.setting);
  const [openModal, setOpenModal] = useState(false);
  const { settingItem } = settings;
  const [dates, setDates] = useState([]);
  const globalSetting = settingItem.find(
    (value) => value.name === "globalSetting"
  );
  const currency = globalSetting?.default_currency || "$";

  const {
    register,
    handleSubmit,
    setValue,
    reset,
    clearErrors,
    formState: { errors },
  } = useForm();

  const onCloseModal = () => {
    console.log("eff")
    clearErrors([])
    setOpenModal(false);
    reset()
  }
  const onSubmit = async (data) => {
    console.log(pg,"pagenumber")
    try {
    setIsSubmitting(true)
    if (imageUrl.length === 0) {
      setTimeout(() => {
        setIsSubmitting(false)
        }, 2000);
      return notifyError("File is empty. Please select a valid file.");
    } else {
    const propertyImages = imageUrl.map((item) => ({
      image: typeof item.image === "string" ? item.image : item.image.image,
    }));
    console.log("Food data", data,pg, discountType, dates);

    let payload = {
      property_id: data.PropertyId,
      name: data.Name,
      type: data.SelectType,
      category: data.Category,
      no_of_skewers: data.no_of_skewers,
      price: data.price,
      cgst: data.cgst,
      sgst: data.sgst,
      description: data?.Description,
      alacarte_images: propertyImages,
    };
    if (data?.id) {
      payload["id"] = data?.id;
    }
    const res = await addAllCarte(payload, data?.id);
    if (res.data.status == true) {
      console.log(res, "dsfs");
      handleClearVariant();
      getAllFoodList(pg || pagenumber);
      if(data?.id) {
        closeDrawer1();
      } else {
      closeDrawer();
      }
      setIsSubmitting(false)
      notifySuccess(res.data.message);
    }
  }
  } catch (err) {
    notifyError(err ? err?.response?.data?.message : err.message);
    setIsSubmitting(false);
  }
    // try {
    //   setIsSubmitting(true);
    //   const couponData = {
    //     title: {
    //       [language]: data.title,
    //     },
    //     couponCode: data.couponCode,
    //     endTime: data.endTime,
    //     minimumAmount: data.minimumAmount,
    //     logo: imageUrl,
    //     lang: language,
    //     status: published ? "show" : "hide",
    //     discountType: {
    //       type: discountType ? "percentage" : "fixed",
    //       value: data.discountPercentage,
    //     },
    //     productType: data.productType,
    //   };
    // } catch (err) {
    //   notifyError(err ? err?.response?.data?.message : err.message);
    //   setIsSubmitting(false);
    //   closeDrawer();
    // }
  };

  const handleClearVariant = () => {
    setImageUrl([]);
    setDates([]);
    setValue("PropertyId", "");
    setValue("Name", "");
    setValue("SelectType", "");
    setValue("Category", "");
    setValue("no_of_skewers", "");
    setValue("price", "");
    setValue("cgst", "");
    setValue("sgst", "");
    setValue("Description", "");
  };

  return {
    register,
    handleSubmit,
    onSubmit,
    errors,
    setImageUrl,
    imageUrl,
    published,
    setPublished,
    currency,
    onCloseModal,
    discountType,
    onCloseModal,
    isSubmitting,
    setIsSubmitting,
    setDiscountType,
    setDates,
    setValue,

    dates,
  };
};

export default useCouponSubmit;
