import { Alert, Input, Select, Textarea } from "@windmill/react-ui";
import DrawerButton from "components/form/DrawerButton";
import Error from "components/form/Error";
import InputArea from "components/form/InputArea";
import LabelArea from "components/form/LabelArea";
import Title from "components/form/Title";
import Uploader from "components/image-uploader/Uploader";
import useCouponSubmit from "hooks/useAddFoodSubmit";
import { t } from "i18next";
import { useEffect, useState } from "react";
import { Scrollbars } from "react-custom-scrollbars-2";
import { getAllCommonProperyList } from "services/Api";

const CouponDrawer = ({ id, getAllFoodList, pagenumber }) => {
  const {
    register,
    handleSubmit,
    onSubmit,
    errors,
    onCloseModal,
    setImageUrl,
    imageUrl,
    setIsSubmitting,
    isSubmitting,
    handleSelectLanguage,
  } = useCouponSubmit(id, getAllFoodList, pagenumber);
  const [propertyList, setpropertyList] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    getPropertyList();
  }, []);
  const getPropertyList = async () => {
    const PropertyList = await getAllCommonProperyList();
    console.log(
      PropertyList?.data?.data?.propertyList,
      "getAllcommonProperyList"
    );
    setpropertyList(PropertyList?.data?.data?.propertyList);
    // setLoading(false);
  };
  const containsSpecialCharsOrNumbers = (value) => {
    return /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?0-9]/.test(value);
  };
  return (
    <>
      <div className="w-full relative  p-6 border-b border-gray-100 bg-gray-50 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300 ">
        {id ? (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("UpdateCoupon")}
            description={t("UpdateCouponDescription")}
          />
        ) : (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("Add Food")}
            description={t(
              "Add your foods and necessary information from here"
            )}
          />
        )}
      </div>

      <Scrollbars className="w-full md:w-7/12 lg:w-8/12 xl:w-8/12 relative dark:bg-gray-700 dark:text-gray-200">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="px-6 pt-8 flex-grow scrollbar-hide w-full max-h-full pb-40">
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Select Property")} />
              <div className="col-span-8 sm:col-span-4">
                <Select
                  {...register(`PropertyId`, {
                    required: "Select Property is required !",
                  })}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                >
                  <option value="" defaultValue hidden>
                    {t("Select property")}
                  </option>
                  {propertyList?.map((pro) => (
                    <option value={pro.id}>{pro.name}</option>
                  ))}
                </Select>
                <Error errorName={errors.PropertyId} pos />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Name")} />
              <div className="col-span-8 sm:col-span-4">
                <Input
                  {...register(`Name`, {
                    required: "Food name required!",
                    validate: (value) =>
                      !containsSpecialCharsOrNumbers(value) ||
                      "Name cannot contain special characters or numbers",
                  })}
                  label="Name"
                  name="Name"
                  type="text"
                  placeholder={t("Name")}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                />
                <Error errorName={errors.Name} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Description")} />
              <div className="col-span-8 sm:col-span-4">
                <Textarea
                  className="border text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                  {...register("Description", {
                    // required: "Food Description required!",
                  })}
                  name="Description"
                  placeholder={t("Description")}
                  rows="4"
                  spellCheck="true"
                />
                <Error errorName={errors.Description} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Select Type")} />
              <div className="col-span-8 sm:col-span-4">
                <Select
                  {...register(`SelectType`, {
                    required: "Select Type is required !",
                  })}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                >
                  <option value="" defaultValue hidden>
                    {t("Select Type")}
                  </option>
                  <option value="food_item">Food Item</option>
                  <option value="bbq">BBQ</option>
                </Select>
                <Error errorName={errors.SelectType} pos />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Select Category")} />
              <div className="col-span-8 sm:col-span-4">
                <Select
                  {...register(`Category`, {
                    required: "Select Category is required !",
                  })}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                >
                  <option value="" defaultValue hidden>
                    {t("Select Category")}
                  </option>
                  <option value="veg">Veg</option>
                  <option value="non_veg">Non Veg</option>
                </Select>
                <Error errorName={errors.Category} pos />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("No Of Skewers")} />
              <div className="col-span-8 sm:col-span-4">
                <Input
                  {...register("no_of_skewers", {
                    required: "No Of Skewers required!",
                    pattern: {
                      value: /^[0-9]*$/,
                      message: "Please enter a valid number.",
                    },
                  })}
                  label="No Of Skewers"
                  name="no_of_skewers"
                  type="text"
                  placeholder={t("No Of Skewers")}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                />
                <Error errorName={errors.no_of_skewers} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Food Banner")} />
              <div className="col-span-8 sm:col-span-4">
                <Uploader
                  imageUrl={imageUrl}
                  setImageUrl={setImageUrl}
                  product
                  folder="product"
                />
                <Alert className="sm12" type="info">
                  Image should be 574 x 348px and Image Size should be less than
                  5mb
                </Alert>
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Amount (include CGST+SGST)")} />
              <div className="col-span-4 sm:col-span-4">
                <Input
                  {...register("price", {
                    required: "price required!",
                    pattern: {
                      value: /^[0-9]*$/,
                      message: "Please enter a valid number.",
                    },
                  })}
                  label={t("Amount")}
                  name="price"
                  type="text"
                  placeholder={t("Enter Amount")}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                />
                <Error errorName={errors.price} />
              </div>
              {/* <div className="col-span-4 sm:col-span-1">
                <Input
                      {...register('cgst', {
                        required: 'cgst required!',
                        pattern: {
                          value: /^[0-9]*$/,
                          message: 'Please cgst a valid number.',
                        },
                      })}
                  label={t("Amount")}
                  name="cgst"
                  type="text"
                  placeholder={t("CGST")}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"

                />
                <Error errorName={errors.cgst} />
              </div> */}
              {/* <div className="col-span-4 sm:col-span-1">
                <Input
                    {...register('sgst', {
                      required: 'sgst required!',
                      pattern: {
                        value: /^[0-9]*$/,
                        message: 'Please sgst a valid number.',
                      },
                    })}
                  label={t("Amount")}
                  name="sgst"
                  type="text"
                  placeholder={t("SGST")}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"

                />
                <Error errorName={errors.sgst} />
              </div> */}
            </div>
          </div>
          <DrawerButton
            imageUrl={imageUrl}
            setImageUrl={setImageUrl}
            onCloseModal={onCloseModal}
            id={id}
            title="Food"
            isSubmitting={isSubmitting}
          />
        </form>
      </Scrollbars>
    </>
  );
};

export default CouponDrawer;
