import React, { useContext, useEffect, useState } from "react";
import {
  Table,
  TableHeader,
  TableCell,
  TableFooter,
  TableContainer,
  Button,
  Card,
  CardBody,
  Pagination,
  Input,
} from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import { FiPlus } from "react-icons/fi";
import useAsync from "hooks/useAsync";
import useToggleDrawer from "hooks/useToggleDrawer";
import NotFound from "components/table/NotFound";
import ProductServices from "services/ProductServices";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import ProductTable from "components/food/FoodDetailsTable";
import MainDrawer from "components/drawer/MainDrawer";
import TableLoading from "components/preloader/TableLoading";
import CouponDrawer from "components/food/addFood";
import { FoodsDetailsList } from "services/Api";

const Products = () => {
  const { title, allId, serviceId, handleDeleteMany, handleUpdateMany } =
    useToggleDrawer();

  const { t } = useTranslation();

  const {
    toggleDrawer,
    lang,
    currentPage,
    searchText,
    category,
    handleSubmitForAll,
    sortedField,
    limitData,
  } = useContext(SidebarContext);

  const [loading, setLoading] = useState(false);
  const [pagenumber, setpagenumber] = useState(1);
  const [FoodsList, setFoodsList] = useState([]);
  const [singleFood, setsingleFood] = useState([]);
  const [wholeCount, setwholeCount] = useState({});
  const { data } = useAsync(() =>
    ProductServices.getAllProducts({
      page: currentPage,
      limit: limitData,
      category: category,
      title: searchText,
      price: sortedField,
    })
  );

  useEffect(() => {
    getAllFoodList(pagenumber);
  }, []);

  const getAllFoodList = async (pagenumber, search = "") => {
    const food = await FoodsDetailsList(pagenumber, search);
    console.log(food.data.data, "food");
    setLoading(false);
    setFoodsList(food?.data?.data);
    setwholeCount(food?.data?.data?.pagination);
  };

  const handleChangePage = (e) => {
    console.log("clicked food", e);
    setpagenumber(e);
    getAllFoodList(e);
  };

  const handleSearch = (search) => {
    getAllFoodList(pagenumber, search);
  };

  return (
    <>
      <PageTitle>{t("Food")}</PageTitle>
      <MainDrawer>
        <CouponDrawer
          pagenumber={pagenumber}
          id={serviceId}
          getAllFoodList={getAllFoodList}
        />
      </MainDrawer>
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody className="">
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-4 lg:gap-6 xl:gap-6  xl:flex"
          >
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Input
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e) => handleSearch(e.target.value)}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
              <button
                type="submit"
                className="absolute right-0 top-0 mt-5 mr-1"
              ></button>
            </div>
            <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
              <div className="w-full md:w-48 lg:w-48 xl:w-48">
                <Button
                  onClick={toggleDrawer}
                  className="w-full rounded-md h-12"
                >
                  <span className="mr-2">
                    <FiPlus />
                  </span>
                  {t("Add Foods")}
                </Button>
              </div>
            </div>
          </form>
        </CardBody>
      </Card>

      {loading ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : FoodsList?.length !== 0 ? (
        <TableContainer className="mb-8 rounded-b-lg">
          <Table>
            <TableHeader>
              <tr>
                <TableCell className="text-left">{"s.no"}</TableCell>
                <TableCell>{t("Property Name")}</TableCell>
                <TableCell>{t("Name")}</TableCell>
                <TableCell className="miw">{t("Description")}</TableCell>
                <TableCell>{t("Type")}</TableCell>
                <TableCell>{t("Category")}</TableCell>
                <TableCell>{t("No Skewers")}</TableCell>
                <TableCell>{t("Price")}</TableCell>
                <TableCell className="miw">{t("Images")}</TableCell>
                {/* <TableCell className="text-center">{t("Status")}</TableCell> */}
                <TableCell className="text-right">{t("Action")}</TableCell>
              </tr>
            </TableHeader>
            <ProductTable
              lang={lang}
              products={FoodsList?.ala_carte}
              getAllFoodList={getAllFoodList}
              singleFood={singleFood}
              pagenumber={pagenumber}
              setsingleFood={setsingleFood}
              wholeCount={wholeCount}
            />
          </Table>
          <TableFooter>
            <Pagination
              totalResults={FoodsList?.pagination?.total}
              resultsPerPage={FoodsList?.pagination?.pageSize}
              onChange={handleChangePage}
              label="Product Page Navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Product" />
      )}
    </>
  );
};

export default Products;
